<template>
  <div class="hello">
    <!-- render field -->
    <div class="flex">
      <div class="choose">
        <div class="blk" v-for="(item, key, index) in blockTypes" :key="index">
          <div v-on:click="selectBlock(key)" >
          <p :class="currentSelection == key ? 'active' : ''">{{item}}</p>
          <block :type="key"></block>
          </div>
        </div>
      </div>
      <div>
        <div v-for="(row, index) in splitMsginChars" :key="index">
          <div class="flex">
            <div v-for="(char, indey) in row" :key="index * 10 + indey">
              <block
                v-on:click.native="(e) => changeBlk(index * 10 + indey)"
                :type="char"
              ></block>
            </div>
          </div>
        </div>
      </div>
    </div>
    <h4>Block count: {{ levelString.length }}</h4>
    <button v-on:click="clearLevel()">Clear level</button>
    <textarea
      v-model="levelString"
      v-on:keyup.enter="changeLevel"
      rows="1"
      cols="50"
      style="margin-left: 10px"
    ></textarea>
  </div>
</template>

<script>
import block from "./block.vue";
export default {
  name: "LevelDisplay",
  data: function () {
    return {
      currentSelection: "0",
      levelString: '111111111111111111110000000000110101LLL011100000001111000011111LLL1111000LLL0001111LLL11110001110000110000LLL0111000100011110011101111110111',
      blockTypes: {
        1: "normalBlock",
        L: "lockedBlock",
        0: "noBlock",
        F: "fixedBlock",
        S: "fastBlock",
        s: "slowBlock",
        W: "paddleWidener",
      },
    };
  },
  components: {
    block,
  },
  props: {
  },
  methods: {
    changeBlk: function (index) {
      this.levelString = this.levelString.substr(0, index) + this.currentSelection + this.levelString.substr(index + 1);
    },
    selectBlock: function (type) {
      console.log(type)
      this.currentSelection = String(type);
    },
    clearLevel: function () {
      // create string with same length as levelString
      let newString = "";
      this.levelString = newString.padEnd(this.levelString.length, "0");
    },
    hoverFunc: function () {
      console.log("hover");
    },
  },
  computed: {
    splitMsg: function () {
      //split the string into groups of 10 chars per row
      return this.levelString.match(/.{1,10}/g);
    },
    splitMsginChars: function () {
      return this.splitMsg.map((row) => row.match(/.{1}/g));
    },
  },
};
</script>

