<template>
  <div class="hello">
      <!-- show Block -->
      <div :class="'blk ' + classFunc">
          
      </div>
  </div>
</template>

<script>
export default {
  name: 'Block',
  props: {
    type: String
  },
  computed: {
    classFunc: function () {
      if (this.type === "1"){
        return "normalBlock";
      } else if (this.type === "F") {
        return "fixedBlock"
      } else if (this.type === "L") {
        return "lockedBlock"
      } else if (this.type === "S") {
        return "fastBlock"
      } else if (this.type === "s") {
        return "slowBlock"
      } else if (this.type === "W") {
        return "paddleWidener"
      } else {
        return "noBlock"
      }      
    }
  }
}
</script>

